
#modulebox 
{
    border: 2px solid black;
    border-radius: 25px;
    width: 62%;
    min-width: 1000px; 
    margin: auto;
    padding: 20px; 
    justify-content: center;
    background-color: #0d1117;
}

#griddisplay
{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 50px;
    grid-auto-rows: minmax(100px, auto);
}

#playercard
{
    border: 2px solid black;
    border-radius: 25px;
    width: 50%;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    background-color: #0d1117;
    min-width: 500px;
    max-width: 600px;
    padding: 20px;
}

#playerbadge{
    border: 2px solid black;
    border-radius: 25px;
    height: 30px;
    display: block;
    margin: auto;
    margin-bottom: 40px;
    font-size: 18px;
    width: 300px;
    background-color:#8D12C1;
}

#purplebutton{
    background-color:#8D12C1;
    border: 2px solid black;
}

#col2{
    background: rgba(33, 33, 33, 0.8);
    border-radius: 25px;
    border: 2px solid black;
    padding: 20px;
    margin: 10px;
    min-width: 200px;
}

#col2 > Button{
    margin: inherit;
}

#wrapAnchor{

    /* These are technically the same, but use both */
    overflow-wrap: break-word;
    word-wrap: break-word;
  
    -ms-word-break: break-all;
    /* This is the dangerous one in WebKit, as it breaks things wherever */
    word-break: break-all;
    /* Instead use this non-standard one: */
    word-break: break-word;
  
    /* Adds a hyphen where the word breaks, if supported (No Blink) */
    -ms-hyphens: auto;
    -moz-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
}

#darklistgroup {
    background: rgba(33, 33, 33, 0.8);
    color: #fff
}